.social-icons-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.social-icons {
  display: flex;
  gap: 20px;
}

.social-icon {
  color: #3498db;
  font-size: 1.5em;
  cursor: pointer;
  transition: color 0.3s ease;
}

.social-icon:hover {
  color: #2980b9;
}